.default:hover:not(:active,:disabled) {
  //background: linear-gradient(rgb(126, 126, 134), $BgColor) rgb(126, 126, 134);
  background: linear-gradient(rgb(126, 126, 134) 6%, $BgColor 50%);
}

.default:active {
  top: 1px;
  background: linear-gradient(rgb(76, 77, 82), rgb(56, 57, 62)) rgb(76, 77, 82);
  box-shadow: 0 0 1px rgba(0, 0, 0, .5) inset,
  0 2px 3px rgba(0, 0, 0, .5) inset,
  0 1px 1px rgba(255, 255, 255, .1);
}


.red {
  background: linear-gradient(rgb(255, 40, 40), rgb(147, 1, 1)) rgb(182, 1, 1);;

}

.default:disabled {
  background: linear-gradient(rgb(202, 202, 202), rgb(126, 126, 126)) rgb(126, 126, 134);
  color: #545454;
}