@import '../common/styles/variables.scss';

.headerBlock {
  background-color: $additionalBgColor;
  position: fixed;
  top: 30px;
  right: 30px;
  display: flex;
  justify-content: flex-end;
  align-items: center;

  .toggle {
    width: 55px;
    height: 55px;
    text-align: center;
    margin-right: 0px;
    text-align: center;
    border-radius: 50%;
    cursor: pointer;
    background: #252c48;
    position: absolute;
    top: 0px;
    right: 0px;
    z-index: 1000;
    box-shadow: 0px 0px 1px 1px #4c84ff;

    .navIcon {
      width: 100%;
      height: 100%;
      padding: 0px 10px;
      position: relative;
      top: 0px;
      left: 0px;
      transition: .5s ease-in-out;
      cursor: pointer;

      span {
        display: block;
        height: 2px;
        width: 35%;
        left: 18px;
        position: absolute;
        background: #fff;
        border-radius: 9px;
        opacity: 1;
        transform: rotate(0deg);
        transition: .25s ease-in-out;

        &:nth-child(1) {
          top: 21px;
        }

        &:nth-child(2) {
          top: 27px;
        }

        &:nth-child(3) {
          top: 27px;
        }

        &:nth-child(4) {
          top: 33px;
        }
      }

      &.open {
        span {
          &:nth-child(1) {
            top: 27px;
            width: 0%;
            left: 50%;
          }

          &:nth-child(2) {
            transform: rotate(45deg);
          }

          &:nth-child(3) {
            transform: rotate(-45deg);
          }

          &:nth-child(4) {
            top: 27px;
            width: 0%;
            left: 50%;
          }
        }
      }
    }
  }

  .menu {
    /*display: flex;*/
    /*justify-content: flex-end;*/
    /*align-items: center;*/
    position: absolute;
    top: 0px;
    right: 0px;
    z-index: 999;
    display: flex;
    border-radius: 50px 50px 50px 50px;
    padding: 0px 100px 0px 55px;
    background: #252c48;
    height: 55px;
    box-shadow: 0px 0px 2px 1px #4c84ff;
    /*visibility: hidden;*/
    /*opacity: 1;*/
    &.close {
      visibility: hidden;
    }
  }
}